.project-card-lightbox {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
}

.project-card-lightbox.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

.project-card-lightbox .lightbox-img {
  max-width: 90%;
  max-height: 80%;
  padding: 4px;
  background-color: black;
  border: 2px solid gainsboro;
}
